import Admin from 'Container/Adminpanel.vue';

const currencyAdd = () => import ('Views/AdminPanel/addcurrency.vue');
const currencyEdit = () => import ('Views/AdminPanel/edit-currency.vue');
const currency = () => import ('Views/AdminPanel/currency.vue');
const rentalInvoice = () => import ('Views/AdminPanel/rentalinvoice.vue');
const Agent = () => import ('Views/AdminPanel/agent.vue');
const agentAdd = () => import ('Views/AdminPanel/addAgent.vue');
const contact = () => import ('Views/AdminPanel/contact.vue');
const servicesInvoice = () => import ('Views/AdminPanel/servicesInvoice.vue');
const servicesAdd = () => import ('Views/AdminPanel/servicesAdd.vue');
const services = () => import ('Views/AdminPanel/services.vue');
const RentalEdit = () => import('Views/AdminPanel/rentalEdit.vue');
const RentalAdd = () => import('Views/AdminPanel/rentalAdd.vue');
const Rental = () => import('Views/AdminPanel/rental.vue');
const CompanyAdd = () => import('Views/AdminPanel/companyAdd.vue');
const CategoriesAdd = () => import('Views/AdminPanel/categoriesAdd.vue');
const CategoriesEdit = () => import('Views/AdminPanel/categoriesEdit.vue');
const DepartmentsEdit = () => import('Views/AdminPanel/departmentsedit.vue');
const departmentsAdd = () => import('Views/AdminPanel/departmentsadd.vue');
const Company = () => import('Views/AdminPanel/company.vue');
const Categories = () => import('Views/AdminPanel/categories.vue');
const Departments = () => import('Views/AdminPanel/departments.vue');
const Reports = () => import('Views/AdminPanel/Reports.vue');
const Invoice = () => import('Views/AdminPanel/Invoices.vue');
const AdminAccount = () => import('Views/AdminPanel/Account.vue');
const Collaboration = () => import('Views/AdminPanel/Collaboration.vue');
const AccountSetting = () => import('Views/AdminPanel/AccountSetting.vue');
const AdminProfile = () => import('Views/AdminPanel/Profile.vue');
const AdminEditProfileInfo = () => import('Views/AdminPanel/EditProfileInfo.vue');
const AdminProducts = () => import('Views/AdminPanel/Products.vue');
const AdminProductsAdd = () => import('Views/AdminPanel/ProductsAdd.vue');
const ProductsEdit = () => import('Views/AdminPanel/ProductsEdit.vue');
const PrivacyPolicy = () => import('Views/AdminPanel/ProductsEdit.vue');

export default {
	path: '/admin-panel',
	component: Admin,
	redirect:'/admin-panel/invoices',
	children:[
		{ 
			path: 'add-currency',
			component: currencyAdd,
			name:'currencyAdd',
		},
		{ 
			path: 'edit-currency/:currency',
			component: currencyEdit,
			name:'currencyEdit',
		},
		{ 
			path: 'currency',
			component: currency,
			name:'currency',
		},
		{ 
			path: 'rental-invoice',
			component: rentalInvoice,
			name:'rentalInvoice',
		},
		{ 
			path: 'agent',
			component: Agent,
			name:'Agent',
		},
		{ 
			path: 'agent-add',
			component: agentAdd,
			name:'agentAdd',
		},
		{ 
			path: 'contact',
			component: contact,
			name:'contact',
		},
		{ 
			path: 'services-invoice',
			component: servicesInvoice,
			name:'servicesInvoice',
		},
		{ 
			path: 'services-add',
			component: servicesAdd,
			name:'servicesAdd',
		},
		{ 
			path: 'services',
			component: services,
			name:'services',
		},
		{ 
			path: 'Rental-Edit/:id',
			component: RentalEdit,
			name:'RentalEdit',
		},
		{ 
			path: 'rental-add',
			component: RentalAdd,
			name:'RentalAdd',
		},
		{ 
			path: 'rental',
			component: Rental,
			name:'Rental',
		},
		{ 
			path: 'company-add',
			component: CompanyAdd, 
			name:'CompanyAdd',
		},
		{ 
			path: 'category-add',
			component: CategoriesAdd, 
			name:'CategoriesAdd',
		},
		{ 
			path: 'category-edit/:id',
			component: CategoriesEdit, 
			name:'CategoriesEdit',
		},
		{ 
			path: 'departments-edit/:id',
			component: DepartmentsEdit, 
			name:'DepartmentsEdit',
		},
		{
			path: 'departments-add',  
			component: departmentsAdd, 
			name:'departmentsAdd'
		},
		{ 
			path: 'company',  
			component: Company, 
			name:'Company'
		},
		{ 
			path: 'categories',  
			component: Categories, 
			name:'Categories'
		},
		{ 
			path: 'Departments',  
			component: Departments, 
			name:'Departments'
		},
		{ 
			path: 'reports',  
			component: Reports, 
			name:'Reports'
		},
		{ 
			path: 'invoices',  
			component: Invoice, 
			name:'Invoice'
		},
		{ 
			path: 'products',  
			component: AdminProducts, 
			name:'AdminProducts'
		},
		{ 
			path: 'product-add',
			component: AdminProductsAdd, 
			name:'AdminProductsAdd'
		},
		{ 
			path: 'product-edit/:id',
			component: ProductsEdit, 
			name:'ProductsEdit',
		},
		{ 
			path: 'account/profile',
			component: AdminAccount, 
			name:'Profile1',
			children: [
				{
					path: '/admin-panel/account/profile',name:'AdminProfile',component: AdminProfile,
				},
				{
					path: '/admin-panel/account/collaboration',name:'Collaboration',component: Collaboration,
				},
				{
					path: '/admin-panel/account/settings',name:'AccountSetting',component: AccountSetting,
				},
				{
					path: '/admin-panel/account/profile/:title',name:'AdminEditProfileInfo',component: AdminEditProfileInfo,
				},
         ],
		},

		{ 
			path: 'privacy-policy',  
			component: PrivacyPolicy, 
			name:'PrivacyPolicy1'
		}
	]
}

